.card {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.card {
    /*margin-top: 10px;*/
    /*margin-bottom: 10px;*/
    box-sizing: border-box;
    border-radius: 2px;
    background-clip: padding-box;
}

.card-post-highlight {
    margin-top: 10px;
    margin-bottom: 10px;
}

.card span.card-title {
    color: #fff;
    font-size: 24px;
    font-weight: 300;
    text-transform: uppercase;
}

.card .card-image {
    position: relative;
    overflow: hidden;
}

.card .card-image img {
    border-radius: 2px 2px 0 0;
    background-clip: padding-box;
    position: relative;
    z-index: -1;
}

.card .card-image span.card-title {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 16px;
}

.card .card-content {
    padding: 8px;
    border-radius: 0 0 2px 2px;
    background-clip: padding-box;
    box-sizing: border-box;
}

.card .card-content-post-highlight {
    padding: 1em;
}

.card .card-content p {
    margin: 0;
}

.card .card-action {
    border-top: 1px solid rgba(160, 160, 160, 0.2);
    padding: 7px;
}

.card .card-action a {
    /*text-transform: uppercase;*/
}

/*custom additions*/
div.card {
    margin-top: 0;
    margin-bottom: 0;
}